import React,{useState} from 'react'

import { Link } from 'react-router-dom'
import { Rating } from 'react-simple-star-rating'

const Product = ({product}) => {
  const [rating, setRating] = useState(product.ratings) // initial rating value


    return (
         // https://m.media-amazon.com/images/I/617NtexaW2L._AC_UY218_.jpg
        <div  className="col-sm-12 col-md-8 col-lg-4 my-3" >
          <div className="card1" >
          <div className="imgBox">
  <img src={product.images.url?product.images.url:"https://upload.wikimedia.org/wikipedia/commons/6/65/No-Image-Placeholder.svg"} alt="mouse corsair" className="mouse"/>
</div>
<div className="contentBox">
<Link to={`/product/${product._id}`} style={{textDecoration:"none"}} > <h3> {product.name}</h3></Link>
  <h2 className="price">${product.price}</h2>
 <div>
                <Rating 
                size={18} 
                ratingValue={rating}
                initialValue={3}
                transition={true}
                allowHover={false}
                readonly={true}
                />
   
                <span id="no_of_reviews">({product.noofreviews} Reviews)</span>
                </div>  
  <Link to={`/product/${product._id}`} className="buy">Buy Now</Link>
</div>



          </div>
        </div>
        
    )
}

export default Product
