import React,{Fragment} from 'react'
import {Link} from 'react-router-dom'
import { useDispatch,useSelector } from 'react-redux';
import { useAlert } from 'react-alert';
import '../App.css';
import Search from './Search';
import { LoadUser, logout } from '../actions/UsreAction';
import { useEffect } from 'react';
import axios from 'axios';
import { Cartget } from '../actions/CartAction';
import store from '../store';
const Header = () => {
  const alert= useAlert();
  const disptach=useDispatch();
  const {user,loading}= useSelector(state=> state.auth)
  const {cart}= useSelector(state=> state.cart)
  const logoutHandler=()=>{
    disptach(logout());
    alert.success('Logged out successfully')
  }
useEffect(() => {
  disptach(LoadUser())
  disptach(Cartget())
}, [])

  
    return (
        <Fragment>
        <div className="Header">
             <nav className="navbar row">
      <div className="col-12 col-md-3">
        <div className="navbar-brand">
          <Link to='/'>
          <img className='logoimg' src="https://images.all-free-download.com/images/graphicwebp/var_logo_template_modern_flat_geometry_design_6920658.webp" alt="" />
          </Link>
        </div>
      </div>

      <div className="col-12 col-md-6 mt-2 mt-md-0"  >
        
        <Search style={{marginLeft:"15px"}}/>
        
      </div>

      <div className="col-12 col-md-3 mt-4 mt-md-0 text-center">
        <Link to='/cart' aria-disabled={user&&cart?true:false} style={{textDecoration:'none'}}>
        <i className="fa fa-shopping-cart" aria-hidden="true"></i>
    
        <span className="ml-2" id="cart_count">{cart?cart.length:0}</span>
        </Link>
        {user?(
          <div className="ml-4 dropdown d-inline">
            <Link to='#' className='btn dropdown-toggle ' style={{color:'white'}}  type='button' id='dropDownMenuButton' data-bs-toggle="dropdown"  aria-haspopup='true' aria-expanded='false'>
            <i className='fas fa-caret-down'></i>
            </Link>
            
      
    
            <figure className='avatar avatar-nav'>
              <img src={user.avatar && user.avatar.url} alt={user && user.name}
              className='rounded-circle'
              />
            </figure>
           
            <span style={{color:'white'}}>{user&& user.name}</span>
            <div className="dropdown-menu" style={{color:'white'}} aria-labelledby='dropDownMenuButton'>
              {
                user&&user.role==='admin'?(
                  <Link className='dropdown-item ' to='/dashboard'>
               Dashboard
              </Link>
                  
                ):
                <Link className='dropdown-item ' to='/order'>
              Orders
            </Link>
              }
              <Link className='dropdown-item ' to='/userProfile'>
              Profile
            </Link>
            <Link className='dropdown-item ' to='/myOrders'>
              My Orders
            </Link>
            <Link className='dropdown-item text-danger' onClick={logoutHandler} to='#'>
              Logout
            </Link>
            
            </div>
          </div>

        ):<Link className="btn" to='/login' id="login_btn">Login</Link>
        }
        

        
      </div>
    </nav>
        </div>
        </Fragment>
    )
}

export default Header
